import React from "react";
import {useState,useEffect} from 'react'

const Edituser = (props) => {
  // console.log(props.data)
  const { userId } = props.match.params;
  // console.log(props.data,props,userId)
  const [newData, setNewData] = useState([])

  var handelChange=(e)=>{
    setNewData({...newData,[e.target.name]:e.target.value})
  }

  // useEffect(()=>{
  //   console.log(tempData)
  // },[tempData])

  useEffect(()=>{
    setNewData({
      id:userId,
      name: props.data[Number(userId) - 1].name,
      email: props.data[Number(userId) - 1].email,
      website: props.data[Number(userId) - 1].website,
    });
  },[])

  var handelUpdate=()=>{
    props.setData([
      ...props.data.slice(0, Number(userId) - 1),
      newData,
      ...props.data.slice((Number(userId) - 1)+1),
    ]);
    props.history.push("/user")
  }

  return (
    <div className="edituser">
      <input
        className="edit"
        onChange={(e) => handelChange(e)}
        value={newData.name}
        name="name"
      ></input>
      <input
        className="edit"
        onChange={handelChange}
        value={newData.email}
        name="email"
      ></input>
      <input
        className="edit"
        onChange={handelChange}
        value={newData.website}
        name="website"
      ></input>
      <button onClick={handelUpdate}>Update</button>
    </div>
  );
}

export default Edituser;
