import React from "react";
// import { useHistory } from "react-router-dom";


const Card = (props) =>{
  // console.log(props)
    var handelEdit = () => {
    props.history.push(`/edituser/${props.id}`);
    };
  return (
    <div className="card">
      <img
        alt="user"
        src={`https://robohash.org/${props.id}/?set=set2&size=180x180`}
      />
      <h2>{props.user.name}</h2>
      <div>{props.user.email}</div>
      <div>{props.user.website}</div>
      <button className="editsingle" onClick={() => handelEdit()}>Edit</button>
    </div>
  );
}


export default Card;
