import React from "react";
import { Link } from "react-router-dom";


const style = {
  commonlink: {
    marginRight: "30px",
    fontSize: "23px",
    color: "white",
  },
  header: {
    fontSize: "50px",
  },
  linkwrap: {
    marginTop: "30px",
  },
};

const Header = () => (
  <div className="header">
    <div className="links" style={style.linkwrap}>
      <Link style={style.commonlink} to="/">
        Home
      </Link>
      <Link style={style.commonlink} to="/user">
        User
      </Link>
      <Link style={style.commonlink} to="/Comments">
        Comments
      </Link>
    </div>
  </div>
);

export default Header;