import React from 'react'
import {useState} from 'react'
import '../App.css'
import Card from './Card.component'



const User = (props) => {
// const {routepro} = props
// console.log(props)
const [searchfield, setserch] = useState("")

var handelSearch=(e)=>{
setserch(e.target.value)
}

var filtereduser = props.data.filter((user) =>
  user.name.toLowerCase().includes(searchfield.toLowerCase())
);

return (
  <div>
    <div className="user-searchwrap">
      <input
        onChange={handelSearch}
        className="searchbox"
        type="search"
        placeholder="Search User"
      ></input>
    </div>
    <div className="cardlist">
      {filtereduser.map((users) => {
        return <Card user={users} key={users.id} id={users.id} {...props} />;
      })}
    </div>
  </div>
);
}

export default User;
